.calendar {
    width: 100%;
    height: 100%;
}
.calendar-todo-container {
    height: 80%;
    overflow: auto;
}
.todo-cal .red {
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(255, 0, 0, 0.4)), to(rgba(255, 255, 255, 0.4))) !important;
    background: -webkit-linear-gradient(top left, rgba(255, 0, 0, 0.4), rgba(255, 255, 255, 0.4)) !important;
    background: -o-linear-gradient(top left, rgba(255, 0, 0, 0.4), rgba(255, 255, 255, 0.4)) !important;
    background: linear-gradient(to bottom right, rgba(255, 0, 0, 0.4), rgba(255, 255, 255, 0.4)) !important;
}
.todo-cal .yellow {
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(243, 243, 21, 0.4)), to(rgba(255, 255, 255, 0.4))) !important;
    background: -webkit-linear-gradient(top left, rgba(243, 243, 21, 0.4), rgba(255, 255, 255, 0.4)) !important;
    background: -o-linear-gradient(top left, rgba(243, 243, 21, 0.4), rgba(255, 255, 255, 0.4)) !important;
    background: linear-gradient(to bottom right, rgba(243, 243, 21, 0.4), rgba(255, 255, 255, 0.4)) !important;
}
.todo-cal .green {
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(68, 240, 0, 0.4)), to(rgba(245, 245, 213, 0.4))) !important;
    background: -webkit-linear-gradient(top left, rgba(68, 240, 0, 0.4), rgba(245, 245, 213, 0.4)) !important;
    background: -o-linear-gradient(top left, rgba(68, 240, 0, 0.4), rgba(245, 245, 213, 0.4)) !important;
    background: linear-gradient(to bottom right, rgba(68, 240, 0, 0.4), rgba(245, 245, 213, 0.4)) !important;
} 
.todo-cal {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 20px;
    display: inline-block;
    width: 80%;
    -webkit-box-shadow: 1px 1px 2px rgb(191, 191, 191);
            box-shadow: 1px 1px 2px rgb(191, 191, 191);
}
.todo-cal-header {
    padding: 4px 0;
    display: block;
    text-align: center;
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(191, 191, 191, 0.4)), to(rgba(245, 245, 213, 0.4))) !important;
    background: -webkit-linear-gradient(top left, rgba(191, 191, 191, 0.4), rgba(245, 245, 213, 0.4)) !important;
    background: -o-linear-gradient(top left, rgba(191, 191, 191, 0.4), rgba(245, 245, 213, 0.4)) !important;
    background: linear-gradient(to bottom right, rgba(191, 191, 191, 0.4), rgba(245, 245, 213, 0.4)) !important;
}
.todo-cal-name {
    text-transform: capitalize;
    font-size: 18px;
}
.todo-cal-body {
    margin: 7px;
}
.todo-cal-date {
    margin: 5px 0;
}
.todo-cal-date-symbol,
.todo-cal-time-symbol,
.todo-cal-note-symbol {
    display: inline-block;
    width: 20px;
    text-align: center;
    margin-right: 5px;
}
.todo-cal-date-value {
}
.todo-cal-time {

}
.todo-cal-time-value {

}
.todo-cal-note {
    overflow: hidden;
}
.todo-cal-note-value {
    margin-left: 5px;
}
.todo-cal-subtask {

}
.todo-cal-subtask-task {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    width: 45%;
    margin: 3px 2.5%;
    text-align: left;
}
.todo-cal-subtask-task span:first-child {
    display: inline-block;
    width: 15px;
}
.todo-cal-subtask-header span {
    display: inline-block;
    width: 45%;
    margin: 0 2.5%;
}


